export default class CustomtableController {
	constructor($state, Message, $q) {
		"ngInject";
		this.$state = $state;
		this.Message = Message;
		this.$q = $q;
		this.customColumn = [
			{
				key: 'storeId',
				name: '',
				checked: true
			},
			{
				key: 'storeName',
				name: '',
				checked: true
			},
			{
				key: 'isPhysicalStoreName',
				name: '',
				checked: true
			},
			{
				key: 'businessType',
				name: '',
				checked: true
			},
			{
				key: 'storePhone',
				name: '',
				checked: true
			},
			{
				key: 'plazaName',
				name: '',
				checked: false
			},
			{
				key: 'merchantName',
				name: '',
				checked: false
			}
		];
		this.operations = [
			{
				name: '',
				action: (row) => {
					console.log(row);
          this.Message.info(', id:' + row.storeId);
				}
			},
			{
				name: '',
				action: (row) => {
					console.log(row);
          this.Message.info(', id:' + row.storeId);
				}
			}
		];

		this.defaultData = {
			"totalCount": 23842,
			"items": [{
				"storeId": 1123,
				"storeName": "DO it",
				"storePhone": "0123-45678910",
				"businessType": "",
				"isPhysicalStoreName": '',
				"plazaName": "",
				"merchantName": ""
			}]
		};
		this.data = this.mockData();
	}

	mockData() {
		let deferred = this.$q.defer();
		deferred.resolve(this.defaultData);
		return deferred.promise;
  	}

	pageChange(pageNo) {
		console.log(pageNo)
	}
}



